$col:187.5px;
$ratio:100%;

.latticeFlex {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.latticeFlexCenter {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    justify-content: center;
}

.latticeFlexItemCenter {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    align-items: center;
}


.latticeFlexColumn {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    flex-direction: column;
}

.latticeCol {
    display: inline-block;
    box-sizing: border-box;
}

.latticeCol1 {
    width: 5%;
}

.latticeCol2 {
    width: 10%;
}

.latticeCol3 {
    width: 15%;
}

.latticeCol4 {
    width: 20%;
}

