    flex-grow: 1;
}

.latticeGrid7 {
    width: $ratio/7;
    flex-grow: 1;
}

.latticeGrid1Grow2x {
    width: $ratio;
    flex-grow: 0.5;
}

.latticeGrid2Grow2x {
    width: $ratio/2;
    flex-grow: 0.5;
}

.latticeGrid3Grow2x {
    width: $ratio/3;
    flex-grow: 0.5;
}

.latticeGrid4Grow2x {
