    flex-grow: 0.5;
}

.latticeGrid7Grow2x {
    width: $ratio/7;
    flex-grow: 0.5;
}


.latticeCenterCol1 {
    width: $col;
}

.latticeCenterCol2 {
    width: $col*2;
}

