.alertMsg {
    position: fixed;
    display: flex;
    justify-content: center;
    top: 10vh;
    left: 45vw;
    z-index: 9999999;

    .box {
        background-color: #ffffff;
        border: 1px solid #acacac91;
        text-align: center;
        border-radius: 5px;
        box-shadow: 0px 0px 2px 2px #d4d4d481;
        min-width: 180px;
        min-height: 55px;
        transition: all 0.3s;
        opacity: 0;

        .title {
            padding: 3px 5px;
            border-bottom: 1px solid #9e9e9e;
            margin-inline: 4px;
            font-size: 8px;
            color: #5a5a5a;
        }

        .text {
            padding: 5px;
            font-size: 10px;
            color: #444444;
        }

        &:hover {
            .title {
                color: #000000;
            }

            .text {
                color: #000000;
            }
        }
    }

    .active {
        opacity: 1;
    }

    .error {
        background-color: #ffdfdf;
        border: 1px solid #a52c2c;

        .title {
            color: #520000;
            border-bottom: 1px solid #c43d3d;
        }

        .text {
            color: #6d0000;
        }
    }

    .success {
        background-color: #ebffdf;
        border: 1px solid #2ca52c;

        .title {
            color: #06420b;
            border-bottom: 1px solid #3dc44f;
        }

        .text {
            color: #0a470f;
        }
    }

    .info {
        background-color: #dffaff;
        border: 1px solid #2ca5a5;

        .title {
            color: #0a0642;
            border-bottom: 1px solid #3dc0c4;
        }

        .text {
            color: #100a47;
        }
    }

    .warring {
        background-color: #fffed4;
        border: 1px solid #9da52c;

        .title {
            color: #471d0a;
            border-bottom: 1px solid #bbc43d;
        }

        .text {
            color: #471d0a;
        }
    }
}