.formRow {
    padding: 7px;

    display: flex;
    align-items: center;
    justify-items: start;
    width: 100%;
    box-sizing: border-box;

    .label {
        margin-left: 1rem;
        margin-right: 10px;
        flex-basis: 100px;
        text-shadow: 1px 0px 1px #000000a6;
    }

    .required {
        color: #d12d3a;

        &::before {
            content: "*";
            margin-right: 2px;
        }
    }

    .text {
        width: 100%;

        button {
            border: 1px solid #49494973;
            border-radius: 6px;
            padding: 3px 15px;
            background-color: #ebebeb;
            color: #1d1d1d;
            box-shadow: 0px 0px 1px 1px #e0e0e0;
            margin-inline: 3px;
            cursor: pointer;

            &:focus {
                outline: #8490ff solid 2px;
                background-color: #f0f0f0;
            }

            &:active {
                background-color: #dfdfdf !important;
            }

            &:hover {
                box-shadow: 0px 0px 2px 1px #92e9ff inset;
                background-color: #f0f0f0;
            }
        }
    }
}

@media (max-width:576px) {
    .formRow {
        display: block;

        .label {
            margin-left: 0;
            margin-right: 0;
            margin-bottom: 5px;
            flex-basis: 100px;
        }
    }
}