.button {
    border: 1px solid #49494973;
    border-radius: 6px;
    padding: 3px 15px;
    background-color: #f1f1f1;
    color: #1d1d1d;
    box-shadow: 0px 0px 1px 1px #e0e0e0;
    margin-inline: 3px;
    cursor: pointer;

    &:focus {
        outline: #8490ff solid 2px;
        background-color: #f0f0f0;
    }

    &:active {
        background-color: #dfdfdf !important;
    }

    &:hover {
        box-shadow: 0px 0px 2px 1px #92e9ff inset;
        background-color: #f0f0f0;
    }
}