.input {
    border: 1px solid #61616173;
    border-radius: 5px;
    padding: 6px 7px 5px 7px;

    font-size: inherit;
    line-height: inherit;

    box-sizing: border-box;
    width: 100%;


    &:focus {
        outline: #8490ff solid 2px;
    }

    &:hover {
        box-shadow: 0px 0px 2px 1px #52dcff inset;
    }

    &::placeholder {
        color: #99b19e;
    }
}