.label {
    padding: 7px;

    display: flex;
    align-items: center;
    justify-items: start;
    box-sizing: border-box;

    .title {
        margin-left: 1.4rem;
        margin-right: 14px;
        flex-basis: 120px;
        font-size: 14px;
    }

    .data {
        width: 100%;
        color: #424242;
        font-size: 12px;
    }

}

@media (max-width:576px) {
    .label {
        display: block;

        .title {
            margin-left: 5px;
            margin-right: 0;
            margin-bottom: 5px;
        }

        .data {
            margin-left: 12px;
        }
    }
}