.tableInfo {
    border: 1px solid #000000;

    .title {
        display: flex;
        justify-content: space-between;

        .line {
            width: 100%;
        }

        .expand {
            text-align: right;
            margin-right: 17px;

            span {
                display: block;
                width: 40px;
                cursor: pointer;
            }
        }

        &:hover {
            background-color: #cdffe0;
        }
    }

}